/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { Component } from "react";
// import "./index.css";
import {
  Input,
  Empty,
  Collapse,
  Modal,
  message,
  Popover,
  Popconfirm,
} from "antd";
import { CaretRightOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import {
  addNewType,
  copyFromStore,
  getQuesStore,
  updateStoreList,
} from "../../../apis/examEdit";
import { getTypeName } from "../../../views/examination";
import action from "../../../store/actions";
interface IProps {
  login_id?: number;
  typeList?: IStoreTypeItem[];
  question_store?: IQuestionOriginItem[];
  init: (backToList?: () => void) => void;
  exam_id?: number;
  scrollToBottom?: () => void;
}
interface IWindowQuestion extends IQuesData {
  isShow: boolean;
}
export interface IWindowStoreTypeItem extends IStoreTypeItem {
  isEdit: boolean;
  question_list: IWindowQuestion[];
}
interface IState {
  question_store: IQuestionOriginItem[];
  newTypeState: boolean;
  newTypeName: string;
  manageState: boolean;
  keywords: string;
  searchResult: IQuesData[];
  focusIndex: number;
  storeWindowList: IWindowStoreTypeItem[];
  // store_type: IStoreTypeItem[];
  // common_store:object[]
}
class QuestionStore extends Component<IProps, IState> {
  state = {
    question_store: [],
    // store_type: [],
    // common_store:[]
    newTypeState: false,
    newTypeName: "",
    manageState: false,
    keywords: "",
    searchResult: [],
    focusIndex: 0,
    storeWindowList: [],
  };
  setStoreData = async () => {
    const { login_id = -1 } = this.props;
    const response: IStoreResponse = await getQuesStore({
      creator_id: login_id,
    });
    const { question_store } = response;
    this.setState({ question_store });
  };
  get reduceStoreData() {
    const { question_store = [] } = this.props;
    // const question_store: IQuestionOriginItem[]  = this.state.question_store;
    const {
      // login_id,
      typeList = [],
    } = this.props;
    if (typeList.length > 0) {
      const temp = typeList.map((type: IStoreTypeItem) => ({
        ...type,
        question_list: question_store
          .filter((item: IQuestionOriginItem) => +item.store_type === type.id)
          .map((item: IQuestionOriginItem) => {
            const {
              id,
              creator_id,
              question_name,
              question_image,
              question_score,
              question_type,
              store_type,
              answer,
            } = item;
            return {
              id,
              creator_id,
              question_name,
              question_image,
              question_score,
              question_type,
              store_type,
              answer,
              option_list: Object.keys(item)
                .filter(
                  (key) =>
                    key.indexOf("option") !== -1 && key.indexOf("image") === -1
                )
                .map((key: string, index: number) => ({
                  [`option${index + 1}`]: item[key],
                }))
                .filter((option) => Object.values(option)[0]),
            };
          }),
      }));
      return temp;
    } else {
      return [];
    }
  }
  getTypeName = getTypeName;
  seachStore = (e: { target: { value: string } }) => {
    const { reduceStoreData } = this;
    this.setState({ keywords: e.target.value }, () => {
      const result: IQuesData[] = [];
      const { keywords } = this.state;
      reduceStoreData.reduce((prev, next) => {
        next.question_list.forEach((item) => {
          if (item.question_name.indexOf(keywords) !== -1) prev.push(item);
        });
        return prev;
      }, result);
      this.setState({
        searchResult: result,
      });
    });
  };
  throttling = (callback: () => void, wait: number) => {
    if (!this.timer) {
      this.timer = setTimeout(() => {
        callback();
        this.timer = null;
      }, wait);
    }
  };
  timer: NodeJS.Timeout | null = null;
  componentDidMount() {
    this.setStoreData();
  }
  render() {
    const { reduceStoreData, seachStore } = this;
    const {
      newTypeState,
      newTypeName,
      manageState,
      keywords,
      searchResult,
      focusIndex,
    } = this.state;
    const storeWindowList: IWindowStoreTypeItem[] = this.state.storeWindowList;
    const { exam_id = -1 } = this.props;
    const { Panel } = Collapse;
    // const storeWindowList = JSON.parse(JSON.stringify(reduceStoreData));
    return (
      <div className="question-store">
        <div className="seach-in-store">
          <Input
            placeholder=""
            value={keywords}
            onChange={seachStore}
            style={{
              background:
                "url(http://192.168.5.104:5008/img/search-icon.png) 8px 8px / 18px 18px no-repeat white",
              paddingLeft: 30,
              height: 35,
            }}
          />
        </div>
        <div className="store-module person">
          <h2></h2>
          <div className="operation">
            <span
              className="new-store"
              style={{ cursor: "pointer" }}
              onClick={() => {
                this.setState({ newTypeState: true });
              }}
            >
              
            </span>
            <Modal
              title=""
              visible={newTypeState}
              centered
              cancelText=""
              okText=""
              onOk={async () => {
                const { login_id = -1 } = this.props;
                const addRes = await addNewType({
                  type_name: newTypeName,
                  creator_id: login_id,
                });
                // console.log(addRes);
                if (addRes.code === "1001") {
                  this.setState({ newTypeState: false });
                  this.props.init();
                } else if (addRes.code === "1002") {
                  message.warning(addRes.msg);
                }
              }}
              onCancel={() => {
                this.setState({ newTypeState: false });
              }}
            >
              <p>
                <span style={{ color: "red" }}>*</span>
              </p>
              <Input
                showCount
                maxLength={10}
                onChange={(e) => {
                  this.setState({ newTypeName: e.currentTarget.value });
                }}
              ></Input>
            </Modal>
            <span
              className="store-manage"
              // style={{ cursor: "not-allowed" }}
              style={{ cursor: "pointer" }}
              onClick={() => {
                this.setState({
                  manageState: true,
                  focusIndex: 0,
                  storeWindowList: this.reduceStoreData.map((item) => ({
                    ...item,
                    isEdit: false,
                    question_list: item.question_list.map((item) => ({
                      ...item,
                      isShow: false,
                    })),
                  })),
                });
              }}
            >
              
            </span>
            <Modal
              style={{ minWidth: 900 }}
              bodyStyle={
                storeWindowList.length > 0
                  ? { minHeight: 500, padding: 0 }
                  : {
                    minHeight: 500,
                    padding: 0,
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                  }
              }
              title={
                <p style={{ marginBottom: 0 }}>
                  <span></span>
                </p>
              }
              visible={manageState}
              centered
              maskClosable={false}
              cancelText=""
              okText=""
              onOk={async () => {
                const { login_id = -1 } = this.props;
                // console.log(storeWindowList);
                const updateRes = await updateStoreList({
                  creator_id: login_id,
                  storeWindowList,
                });
                // console.log(updateRes.code);
                if (updateRes.code === "1001") {
                  this.setStoreData();
                }
                this.setState({ manageState: false });
              }}
              onCancel={() => {
                this.setState({ manageState: false });
              }}
            >
              {storeWindowList.length > 0 ? (
                <div className="store-manage-window">
                  <div className="store-type-list">
                    <ul>
                      {storeWindowList.map(
                        (item: IWindowStoreTypeItem, index: number) => (
                          <li
                            key={item.id}
                            className={focusIndex === index ? "focus" : ""}
                            onClick={() => {
                              this.setState({ focusIndex: index });
                            }}
                          >
                            {item.isEdit ? (
                              <Input
                                size="small"
                                value={item.type_name}
                                onChange={(e) => {
                                  item.type_name = e.currentTarget.value;
                                  this.setState({ storeWindowList });
                                }}
                                autoFocus={true}
                                onBlur={() => {
                                  storeWindowList[index].isEdit = false;
                                  this.setState({ storeWindowList });
                                }}
                              />
                            ) : (
                              <>
                                <span className="store-name">
                                  {item.type_name}
                                </span>
                                <span className="store-count">
                                  {item.question_list?.length}
                                </span>
                                <Popover
                                  placement="right"
                                  overlayInnerStyle={{ padding: 0 }}
                                  overlayClassName="store-manage-window-type-operation"
                                  content={
                                    <ul
                                      className="store-manage-window-type-operation-list"
                                      style={{ marginBottom: 0 }}
                                    >
                                      <li
                                        onClick={() => {
                                          item.isEdit = true;
                                          this.setState({ storeWindowList });
                                        }}
                                      >
                                        
                                      </li>
                                      <Popconfirm
                                        placement="right"
                                        title=""
                                        onConfirm={() => {
                                          storeWindowList.splice(index, 1);
                                          this.setState({ storeWindowList });
                                        }}
                                        okText=""
                                        cancelText=""
                                      >
                                        <li></li>
                                      </Popconfirm>
                                    </ul>
                                  }
                                  trigger="hover"
                                >
                                  <span className="store-operation iconfont icon-more"></span>
                                </Popover>
                              </>
                            )}
                          </li>
                        )
                      )}
                    </ul>
                  </div>
                  <div
                    className={
                      storeWindowList[focusIndex].question_list.length > 0
                        ? "store-question-list"
                        : "store-question-list empty"
                    }
                  >
                    {storeWindowList[focusIndex].question_list.length > 0 ? (
                      <ul>
                        {storeWindowList[focusIndex].question_list?.map(
                          (item: IWindowQuestion, index: number) => (
                            <li key={item.id} className="question-item">
                              <div className="question-type">
                                {getTypeName(item.question_type)}
                              </div>
                              <div className="question-name">
                                {item.question_name}
                              </div>
                              {item.isShow ? (
                                <div
                                  className="option"
                                  style={
                                    item.question_type === "4" ||
                                    item.question_type === "5"
                                      ? { paddingLeft: 0, marginTop: 10 }
                                      : { marginTop: 18 }
                                  }
                                >
                                  {item.question_type === "1" ||
                                  item.question_type === "2" ||
                                  item.question_type === "3" ? (
                                      item.option_list.map((option, index) => (
                                        <p key={index}>
                                          <input
                                            type={
                                              item.question_type === "2"
                                                ? "checkbox"
                                                : "radio"
                                            }
                                            disabled
                                            readOnly
                                            style={{ marginRight: 10 }}
                                          />
                                          <span>
                                            {option[`option${index + 1}`]}
                                          </span>
                                          {item.question_type !== "2" &&
                                        item.answer === `${index}` ? (
                                              <span
                                                style={{
                                                  paddingLeft: 10,
                                                  color: "#00bf6f",
                                                }}
                                              >
                                            ()
                                              </span>
                                            ) : (
                                              ""
                                            )}
                                          {item.question_type === "2" &&
                                        item.answer.indexOf(`${index}`) !==
                                          -1 ? (
                                              <span
                                                style={{
                                                  paddingLeft: 10,
                                                  color: "#00bf6f",
                                                }}
                                              >
                                            ()
                                              </span>
                                            ) : (
                                              ""
                                            )}
                                        </p>
                                      ))
                                    ) : item.question_type === "4" ? (
                                      item.option_list.map((option, index) => (
                                        <p
                                          key={index}
                                          style={{
                                            flexDirection: "column",
                                            alignItems: "flex-start",
                                          }}
                                        >
                                          <span style={{ paddingLeft: 10 }}>
                                            {option[`option${index + 1}`]}
                                          </span>
                                          <input
                                            type="text"
                                            style={{
                                              border: "1px solid #eaeaea",
                                              display: "block",
                                              margin: "5px 0 0",
                                            }}
                                          />
                                        </p>
                                      ))
                                    ) : item.question_type === "5" ? (
                                      <p>
                                        <textarea
                                          name=""
                                          id=""
                                          cols={21}
                                          rows={2}
                                        ></textarea>
                                      </p>
                                    ) : (
                                      <></>
                                    )}
                                </div>
                              ) : (
                                ""
                              )}
                              <div
                                className="show-flag hidden"
                                onClick={() => {
                                  item.isShow = !item.isShow;
                                  this.setState({ storeWindowList });
                                }}
                              >
                                <span style={{ cursor: "pointer" }}>
                                  {item.isShow ? "" : ""}
                                </span>
                                <span
                                  className={
                                    item.isShow
                                      ? "iconfont icon-paixu-shang"
                                      : "iconfont icon-paixu-xia"
                                  }
                                ></span>
                              </div>
                              <span
                                className="iconfont icon-shanchu"
                                onClick={() => {
                                  storeWindowList[
                                    focusIndex
                                  ].question_list.splice(index, 1);
                                  this.setState({ storeWindowList });
                                }}
                              ></span>
                            </li>
                          )
                        )}
                      </ul>
                    ) : (
                      <Empty description="" />
                    )}
                  </div>
                </div>
              ) : (
                <Empty description="" />
              )}
            </Modal>
          </div>
        </div>
        <div className="store-list-module person">
          {keywords ? (
            <>
              {searchResult.length > 0 ? (
                <ul>
                  {searchResult.map((result: IQuesData) => (
                    <li
                      className="question-item-search"
                      key={result.id}
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        padding: "2px 2px 2px 10px",
                      }}
                    >
                      <span
                        style={{ cursor: "pointer" }}
                        onClick={async (e) => {
                          e.stopPropagation();
                          const copyRes = await copyFromStore({
                            exam_id,
                            id: result.id ? result.id : -1,
                          });
                          // console.log(copyRes);
                          if (copyRes.code === "1001") {
                            this.props.init(() => {
                              if (this.props.scrollToBottom) {
                                this.props.scrollToBottom();
                              }
                            });
                          }
                        }}
                      >
                        {result.question_name.indexOf(keywords) === 0 ? (
                          <>
                            <span className="highLight">{keywords}</span>
                            <span>
                              {result.question_name.replace(keywords, ``)}
                            </span>
                          </>
                        ) : result.question_name.indexOf(keywords) +
                            keywords.length >=
                          result.question_name.length ? (
                            <>
                              <span>
                                {result.question_name.replace(keywords, ``)}
                              </span>
                              <span className="highLight">{keywords}</span>
                            </>
                          ) : (
                            <>
                              {result.question_name.split(keywords)[0]}
                              <span className="highLight">{keywords}</span>
                              {
                                result.question_name.split(keywords)[
                                  result.question_name.split(keywords).length - 1
                                ]
                              }
                            </>
                          )}
                      </span>
                      <Popover
                        placement="right"
                        trigger="hover"
                        overlayClassName="store-question-preview"
                        content={
                          <div className="preview-question">
                            <div className="preview-title">
                              {result.question_name}
                            </div>
                            {result.question_type === "5" ? (
                              <textarea cols={21} rows={2}></textarea>
                            ) : (
                              <ul className="preview-option">
                                {result.option_list.map((option, index) => (
                                  <li
                                    key={index}
                                    style={{
                                      display: `${
                                        result.question_type !== "4"
                                          ? "flex"
                                          : "block"
                                      }`,
                                      alignItems: "center",
                                      padding: "3px 0",
                                    }}
                                  >
                                    {result.question_type === "1" ||
                                    result.question_type === "2" ||
                                    result.question_type === "3" ? (
                                        <>
                                          <input
                                            style={{ marginRight: 5 }}
                                            readOnly
                                            disabled
                                            type={
                                              result.question_type === "2"
                                                ? "checkbox"
                                                : "radio"
                                            }
                                          />
                                          <span>
                                            {option[`option${index + 1}`]}
                                          </span>
                                          {index === +result.answer ||
                                        result.answer.indexOf(`${index}`) !==
                                          -1 ? (
                                              <span
                                                style={{
                                                  color: "rgb(0, 191, 111)",
                                                  fontSize: 12,
                                                  paddingLeft: 5,
                                                }}
                                              >
                                            
                                              </span>
                                            ) : (
                                              ""
                                            )}
                                        </>
                                      ) : (
                                        ""
                                      )}
                                    {result.question_type === "4" ? (
                                      <>
                                        <span>
                                          {option[`option${index + 1}`]}
                                        </span>
                                        <input
                                          type="text"
                                          readOnly
                                          disabled
                                          style={{
                                            border: "1px solid #ccc",
                                            display: "block",
                                          }}
                                        />
                                      </>
                                    ) : (
                                      ""
                                    )}
                                  </li>
                                ))}
                              </ul>
                            )}
                          </div>
                        }
                      >
                        <span
                          className="view iconfont icon-chakan"
                          style={{ cursor: "pointer" }}
                        ></span>
                      </Popover>
                    </li>
                  ))}
                </ul>
              ) : (
                <Empty description="" />
              )}
            </>
          ) : (
            <>
              {reduceStoreData.length > 0 ? (
                <Collapse
                  bordered={false}
                  // defaultActiveKey={["1"]}
                  expandIcon={({ isActive }) => (
                    <CaretRightOutlined rotate={isActive ? 90 : 0} />
                  )}
                  className="site-collapse-custom-collapse"
                >
                  {reduceStoreData.map((store_type) => (
                    <Panel
                      key={store_type.id}
                      header={store_type.type_name}
                      extra={<span>{store_type.question_list.length}</span>}
                      className="site-collapse-custom-panel"
                    >
                      <div className="store-question-list">
                        {store_type.question_list.map((question) => (
                          <div
                            style={{ cursor: "auto" }}
                            className="question-item"
                            key={question.id}
                          >
                            <span
                              style={{ cursor: "pointer" }}
                              onClick={async (e) => {
                                e.stopPropagation();
                                const copyRes = await copyFromStore({
                                  exam_id,
                                  id: question.id,
                                });
                                // console.log(copyRes);
                                if (copyRes.code === "1001") {
                                  this.props.init(() => {
                                    if (this.props.scrollToBottom) {
                                      this.props.scrollToBottom();
                                    }
                                  });
                                }
                              }}
                            >
                              {question.question_name}
                            </span>
                            <Popover
                              placement="right"
                              trigger="hover"
                              overlayClassName="store-question-preview"
                              content={
                                <div className="preview-question">
                                  <div className="preview-title">
                                    {question.question_name}
                                  </div>
                                  {question.question_type === "5" ? (
                                    <textarea cols={21} rows={2}></textarea>
                                  ) : (
                                    <ul className="preview-option">
                                      {question.option_list.map(
                                        (option, index) => (
                                          <li
                                            key={index}
                                            style={{
                                              display: `${
                                                question.question_type !== "4"
                                                  ? "flex"
                                                  : "block"
                                              }`,
                                              alignItems: "center",
                                              padding: "3px 0",
                                            }}
                                          >
                                            {question.question_type === "1" ||
                                            question.question_type === "2" ||
                                            question.question_type === "3" ? (
                                                <>
                                                  <input
                                                    style={{ marginRight: 5 }}
                                                    readOnly
                                                    disabled
                                                    type={
                                                      question.question_type ===
                                                    "2"
                                                        ? "checkbox"
                                                        : "radio"
                                                    }
                                                  />
                                                  <span>
                                                    {option[`option${index + 1}`]}
                                                  </span>
                                                  {index === +question.answer ||
                                                question.answer.indexOf(
                                                  `${index}`
                                                ) !== -1 ? (
                                                      <span
                                                        style={{
                                                          color: "rgb(0, 191, 111)",
                                                          fontSize: 12,
                                                          paddingLeft: 5,
                                                        }}
                                                      >
                                                    
                                                      </span>
                                                    ) : (
                                                      ""
                                                    )}
                                                </>
                                              ) : (
                                                ""
                                              )}
                                            {question.question_type === "4" ? (
                                              <>
                                                <span>
                                                  {option[`option${index + 1}`]}
                                                </span>
                                                <input
                                                  type="text"
                                                  readOnly
                                                  disabled
                                                  style={{
                                                    border: "1px solid #ccc",
                                                    display: "block",
                                                  }}
                                                />
                                              </>
                                            ) : (
                                              ""
                                            )}
                                          </li>
                                        )
                                      )}
                                    </ul>
                                  )}
                                </div>
                              }
                            >
                              <span
                                className="view iconfont icon-chakan"
                                style={{ cursor: "pointer" }}
                              ></span>
                            </Popover>
                          </div>
                        ))}
                      </div>
                    </Panel>
                  ))}
                </Collapse>
              ) : (
                <Empty description="" />
              )}
            </>
          )}
        </div>
      </div>
    );
  }
}
interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(QuestionStore);
