import React, { Component } from "react";
import "./index.less";
import QuestionTypeList from "./children/question-type-list";
import QuestionStore from "./children/question-store";
import ExamOutline from "./children/exam-outline";
interface IProps {
  init: (backToList?: () => void) => void;
  // [key: string]: string;
}
interface IState {
  activeTab: number;
  tabList: string[];
}
export default class ExamLeftNav extends Component<IProps, IState> {
  state = {
    activeTab: 0,
    tabList: ["", "", ""],
  };
  changeTab = (index: number) => {
    this.setState({ activeTab: index });
  };
  get navBody() {
    const { activeTab } = this.state;
    switch (activeTab) {
    case 0:
      return <QuestionTypeList />;
    case 1:
      return <QuestionStore init={this.props.init} />;
    case 2:
      return <ExamOutline />;
    default:
      <QuestionTypeList />;
    }
  }
  render() {
    const { activeTab, tabList } = this.state;
    const { changeTab, navBody } = this;
    return (
      <div className="left-nav-box">
        <div className="tab-nav">
          {tabList.map((item, index) => (
            <span
              key={index}
              className={index === activeTab ? "active" : ""}
              onClick={() => {
                changeTab(index);
              }}
            >
              {item}
            </span>
          ))}
        </div>
        <div className="nav-body">{navBody}</div>
      </div>
    );
  }
}
