import React, { Component } from "react";
import { connect } from "react-redux";
import action from "../../../store/actions";

interface IProps {
  questionList?: IQuesData[];
  scrollToActiveQues?: (index: number) => void;
}

class ExamOutline extends Component<IProps> {
  render() {
    const { questionList, scrollToActiveQues } = this.props;
    return (
      <div className="outline">
        <ul>
          {questionList?.map((item, index: number) => (
            <li
              key={item.id}
              onClick={(e) => {
                e.stopPropagation();
                scrollToActiveQues ? scrollToActiveQues(index) : "";
              }}
            >{`${index + 1}. ${item.question_name}`}</li>
          ))}
        </ul>
      </div>
    );
  }
}
interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(ExamOutline);
