import React, { Component } from "react";
import "./index.less";
import { Select, InputNumber, Switch, Checkbox } from "antd";
import { connect } from "react-redux";
// import { IQuesData } from "../../views/examination";
import {
  changeQuestionType,
  changeQuestionScore,
  changeQuestionAnswer,
} from "../../apis/examEdit";
import action from "../../store/actions";
interface IProps {
  exam_id?: number;
  question_focus?: number;
  ques_type?: string;
  setQuesType: (payload: { ques_type: string }) => void;
  setFocusMsg: (payload: IFocusQuestion) => void;
  changeShowSerial?: (payload: { showSerial: boolean }) => void;
  init: () => void;
  focusMSG?: {
    id: number;
    question_name?: string;
    question_type?: string;
    question_score?: number;
    answer?: string;
    option_list: IOptionItem[];
  };
  showSerial?: boolean;
}
interface IState {
  typeList: string[];
}
class ExamRightNav extends Component<IProps, IState> {
  state = {
    typeList: ["", "", "", "", ""],
  };
  changeType = async (ques_type: string) => {
    const { focusMSG, setQuesType, init, setFocusMsg, exam_id } = this.props;
    const { getType } = this;
    setQuesType({ ques_type });
    const changeTypeRes = await changeQuestionType({
      exam_id: exam_id ? exam_id : -1,
      id: focusMSG ? (focusMSG.id ? focusMSG.id : -1) : -1,
      type: getType(ques_type),
    });
    // console.log(changeTypeRes);
    if (changeTypeRes.code === "1001") {
      const option_list = focusMSG?.option_list || [];
      setFocusMsg({
        ...focusMSG,
        id: focusMSG?.id ? focusMSG?.id : -1,
        option_list,
        question_type: getType(ques_type),
        answer:
          getType(ques_type) === "1" ||
          getType(ques_type) === "2" ||
          getType(ques_type) === "3"
            ? "0"
            : "",
      });
      init();
    }
  };
  changeScore = async (question_score: number) => {
    const { setFocusMsg, focusMSG, exam_id } = this.props;
    setFocusMsg({
      ...focusMSG,
      id: focusMSG?.id ? focusMSG.id : -1,
      option_list: focusMSG?.option_list ? focusMSG?.option_list : [],
      question_score,
    });
    this.debounce(async () => {
      await changeQuestionScore({
        exam_id: exam_id ? exam_id : -1,
        id: focusMSG?.id ? focusMSG.id : -1,
        score: question_score,
      });
    }, 300);
  };
  changeShowSerial = (showSerial: boolean) => {
    this.props.changeShowSerial
      ? this.props.changeShowSerial({ showSerial })
      : "";
  };
  getType = (type: string) => {
    switch (type) {
    case "":
      return "1";
    case "":
      return "2";
    case "":
      return "3";
    case "":
      return "4";
    case "":
      return "5";
    default:
      return "1";
    }
  };
  timer: NodeJS.Timeout | null = null;
  debounce = (callback: () => void, time: number) => {
    // console.log(this.timer);
    if (this.timer) {
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(() => {
      callback();
    }, time);
  };
  render() {
    const { Option } = Select;
    const { typeList } = this.state;
    const { changeType, changeScore, changeShowSerial } = this;
    const {
      ques_type,
      question_focus,
      focusMSG,
      exam_id,
      setFocusMsg,
      init,
      showSerial,
    } = this.props;
    const indexType = "ABCDEFGHIJ";
    const option_list = focusMSG?.option_list
      ? focusMSG?.option_list.filter((item) => !!item.option_name)
      : [];
    // console.log(focusMSG);
    return (
      <div className="exam-right-box">
        <div className="title" style={{ borderBottom: "1px solid #d8d8d8" }}>
          
        </div>
        {question_focus === -1 ? (
          ""
        ) : (
          <div className="question-info">
            <h2 className="type">{ques_type}</h2>
            <p className="set"></p>
            <div className="changeType">
              <span></span>
              <Select
                style={{ width: 100, float: "right" }}
                onChange={changeType}
                value={ques_type}
                size="small"
              >
                {typeList.map((item, index) => (
                  <Option value={item} key={index}>
                    {item}
                  </Option>
                ))}
              </Select>
            </div>
            {ques_type === "" ||
            ques_type === "" ||
            ques_type === "" ? (
                <div className="answer">
                  <p></p>
                  <div className="answer-option">
                    {ques_type === "" || ques_type === "" ? (
                      <Select
                        size="small"
                        value={focusMSG?.answer}
                        // defaultValue="A.1"
                        onChange={async (value) => {
                          const changeAnswerRes = await changeQuestionAnswer({
                            exam_id: exam_id ? exam_id : -1,
                            id: focusMSG ? (focusMSG.id ? focusMSG.id : -1) : -1,
                            answer: value,
                          });
                          if (changeAnswerRes.code === "1001") {
                            setFocusMsg({
                              ...focusMSG,
                              id: focusMSG?.id ? focusMSG?.id : -1,
                              option_list: focusMSG?.option_list
                                ? focusMSG?.option_list
                                : [],
                              answer: changeAnswerRes.answer,
                            });
                            init();
                          }
                        }}
                        style={{ width: 223 }}
                      >
                        {option_list.length > 0 ? (
                          option_list.map((item, index: number) => (
                            <Option
                              value={`${index}`}
                              key={`${index}_${item.option_name}`}
                            >
                              {`${showSerial ? `${indexType[index]}.` : ""}${
                                item.option_name
                              }`}
                            </Option>
                          ))
                        ) : (
                          <></>
                        )}
                      </Select>
                    ) : (
                      <></>
                    )}

                    {ques_type === "" ? (
                      <>
                        {option_list.length > 0 ? (
                          option_list.map((item, index) => (
                            <div key={`${index}_${item.option_name}`}>
                              <Checkbox
                                checked={
                                  focusMSG?.answer?.indexOf(`${index}`) !== -1
                                }
                                onChange={async () => {
                                  const answerArr = focusMSG?.answer?.split(",");
                                  const i = answerArr
                                    ? answerArr.indexOf(`${index}`)
                                    : -1;
                                  // console.log(answerArr?[i]);
                                  // answerArr[i]
                                  if (i === -1) {
                                    answerArr?.push(`${index}`);
                                  } else {
                                    answerArr?.splice(i, 1);
                                  }
                                  // answerArr?.sort((a, b) => +a - +b).join(",");
                                  // .concat(`${index}`);
                                  // console.log();
                                  const changeAnswerRes =
                                  await changeQuestionAnswer({
                                    exam_id: exam_id ? exam_id : -1,
                                    id: focusMSG
                                      ? focusMSG.id
                                        ? focusMSG.id
                                        : -1
                                      : -1,
                                    answer: answerArr
                                      ? answerArr
                                        .sort((a, b) => +a - +b)
                                        .join(",")
                                      : "0",
                                  });
                                  if (changeAnswerRes.code === "1001") {
                                    setFocusMsg({
                                      ...focusMSG,
                                      id: focusMSG?.id ? focusMSG?.id : -1,
                                      option_list: focusMSG?.option_list
                                        ? focusMSG?.option_list
                                        : [],
                                      answer: changeAnswerRes.answer,
                                    });
                                    init();
                                  }
                                }}
                              >{`${showSerial ? `${indexType[index]}.` : ""}${
                                  item.option_name
                                }`}</Checkbox>
                            </div>
                          ))
                        ) : (
                          <></>
                        )}
                      </>
                    ) : (
                      <></>
                    )}
                  </div>
                </div>
              ) : (
                ""
              )}
            <div className="score">
              <span></span>
              <div>
                <InputNumber
                  min={1}
                  max={100}
                  size="small"
                  // defaultValue={5}
                  value={focusMSG?.question_score}
                  style={{ width: 62, height: 26 }}
                  onChange={changeScore}
                />
                <span className="padding"></span>
              </div>
            </div>
            {ques_type === "" ||
            ques_type === "" ||
            ques_type === "" ? (
                <div className="option-type">
                  <p style={{ marginBottom: 5 }}>
                    <span></span>
                    <Switch
                      checked={showSerial}
                      size="small"
                      onClick={changeShowSerial}
                    />
                  </p>
                </div>
              ) : (
                ""
              )}
          </div>
        )}
      </div>
    );
  }
}

interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(ExamRightNav);
