import React, { Component } from "react";
import {
  Upload,
  message,
  Modal,
  Popover,
  Input,
  Popconfirm,
  Button,
} from "antd";
import { connect } from "react-redux";
import {
  RcFile,
  UploadChangeParam,
  UploadFile,
} from "antd/lib/upload/interface";
// import { IQuesData } from "../../../views/examination";
import {
  deleteQuestion,
  addNewType,
  addQuesToStore,
  cancelCollect,
  removeImage,
  addOption,
  removeOption,
  copyFromTemp,
} from "../../../apis/examEdit";
import * as compress from "../../../utils/compress";
import action from "../../../store/actions";

interface IProps {
  exam_id?: number;
  login_id?: number;
  index: number;
  type: string;
  question_data: IQuesData;
  focusMSG?: IFocusQuestion;
  hiddenHolder?: IQuesData[];
  setHiddenHolder?: (payload: { hiddenHolder: IQuesData[] }) => void;
  setFocusMsg: (payload: {
    id: number;
    question_name?: string;
    option_list?: Array<{ [key: string]: string }>;
  }) => void;
  updateQuesList?: (payload: {
    id: number;
    index: number;
    imgUrl: string;
  }) => void;
  showSerial?: boolean;
  typeList?: ITypeList;
  setQuesFocus: (payload: { index: number }) => void;
  init: (backToList?: () => void) => void;
  scrollToBottom?: () => void;
}
interface IState {
  defaultData: IQuesData;
  imageUid: string;
  fileType: string;
  acceptFileType: string;
  modalState: boolean;
  addNewTypeStatus: boolean;
  newTypeName: string;
  popVisi: boolean;
}
declare type BeforeUploadValueType = void | boolean | string | Blob | File;
interface IMyInfo extends UploadFile<object> {
  id: number;
  index: number;
  code: string;
}
class Choice extends Component<IProps, IState> {
  state = {
    defaultData: {
      id: -1,
      question_name: "",
      question_image: "",
      question_type: "",
      question_score: 5,
      store_type: -1,
      answer: "0",
      option_list: [
        { option_name: "1", option_image: "" },
        { option_name: "2", option_image: "" },
        { option_name: "3", option_image: "" },
        { option_name: "4", option_image: "" },
      ],
    },
    imageUid: "",
    fileType: "",
    acceptFileType: ".jpg,.jpeg,.gif,.png,.bmp,.webp",
    modalState: false,
    addNewTypeStatus: false,
    newTypeName: "",
    popVisi: false,
  };
  handleVisibleChange = (popVisi: boolean) => {
    this.setState({ popVisi });
  };
  addStoreAndClose = async (store_type: number) => {
    const { question_data, login_id = -1, init } = this.props;
    const addQuesRes = await addQuesToStore({
      id: question_data.id || -1,
      creator_id: login_id,
      store_type_id: store_type,
    });
    // console.log(addQuesRes.code);
    if (addQuesRes.code === "1001") {
      init();
      message.success("");
      this.setState({ popVisi: false });
    }
  };
  getQuesType = (type: string) => {
    switch (type) {
    case "1":
      return "radio";
    case "2":
      return "checkbox";
    case "3":
      return "radio";
    default:
      return "";
    }
  };
  select = (e: { target: { select: () => void } }) => {
    e.target.select();
  };
  imageUploadBefore = (
    file: RcFile
  ): BeforeUploadValueType | Promise<BeforeUploadValueType> =>
    new Promise((resolve, reject) => {
      this.setState({ imageUid: file.uid, fileType: file.type }, () => {
        const { acceptFileType } = this.state;
        if (
          acceptFileType.indexOf(
            file.type.slice(file.type.indexOf("/") + 1)
          ) === -1
        ) {
          message.info("");
          reject();
          return;
        }
        if (file.size > 1024 * 1024 * 2) {
          if (compress.support) {
            compress.zip(
              [file],
              { scale: 0.92 },
              function (scaleFile: RcFile[]) {
                if (scaleFile[0].size > 1024 * 1024 * 2) {
                  message.info("");
                  reject();
                  return;
                } else {
                  resolve(scaleFile[0]);
                  return;
                }
              }
            );
          } else {
            message.info("2M");
            reject();
            return;
          }
        } else {
          // console.log("upload");
          resolve(file);
        }
      });
    });
  imageUploadChange = (info: UploadChangeParam<UploadFile<IMyInfo>>): void => {
    if (info.file?.status === "done") {
      const { type, uid } = info.file;
      const imgUrl = `${type?.replace("image/", `/upload/${uid}.`)}`;
      if (this.props.updateQuesList && info.file.response?.code === "1001") {
        this.props.updateQuesList({
          id: info.file.response?.id ? info.file.response?.id : -1,
          index: info.file.response?.index ? info.file.response?.index : -1,
          imgUrl,
        });
      }
    }
  };
  deleteSure = async () => {
    const { focusMSG, exam_id = -1 } = this.props;
    // console.log(focusMSG, exam_id);
    const deleteRes = await deleteQuestion({
      exam_id,
      id: focusMSG?.id ? focusMSG?.id : -1,
    });
    // console.log(deleteRes.code);
    if (deleteRes.code === "1001") {
      this.setState({ modalState: false });
    }
  };
  render() {
    const {
      getQuesType,
      select,
      imageUploadBefore,
      imageUploadChange,
      deleteSure,
      handleVisibleChange,
      addStoreAndClose,
    } = this;
    const {
      imageUid,
      fileType,
      acceptFileType,
      modalState,
      addNewTypeStatus,
      newTypeName,
      popVisi,
    } = this.state;
    const {
      login_id = -1,
      exam_id = -1,
      index,
      type,
      focusMSG,
      setFocusMsg,
      hiddenHolder = [],
      setHiddenHolder,
      showSerial,
      typeList = [],
      init,
    } = this.props;
    const indexType = "ABCDEFGHIJ";
    const data = this.props.question_data || this.state.defaultData;
    const typeListContent = (
      <div className="type-window">
        {typeList.length > 0 ? (
          <ul>
            {typeList.map((item) => (
              <li key={item.id}>
                <span>{item.type_name}</span>
                <span
                  className="add-type"
                  onClick={() => {
                    addStoreAndClose(item.id);
                  }}
                >
                  
                </span>
              </li>
            ))}
          </ul>
        ) : (
          ""
        )}
        {addNewTypeStatus ? (
          <div className="new-type-name">
            <Input
              style={{ marginTop: 5, height: 26, fontSize: 14, paddingLeft: 7 }}
              showCount
              maxLength={10}
              placeholder=""
              value={newTypeName}
              onChange={(e) => {
                this.setState({ newTypeName: e.currentTarget.value });
              }}
            />
            <div
              className="new-type-btn"
              style={{ textAlign: "right", cursor: "default" }}
            >
              <span
                className="cancel"
                style={{ marginRight: 10, cursor: "pointer" }}
                onClick={() => {
                  this.setState({ addNewTypeStatus: false });
                }}
              >
                
              </span>
              <span
                className="create"
                style={{ cursor: "pointer", color: "#2672ff" }}
                onClick={async () => {
                  const addRes = await addNewType({
                    type_name: newTypeName,
                    creator_id: login_id,
                  });
                  // console.log(addRes);
                  if (addRes.code === "1001") {
                    this.setState({ addNewTypeStatus: false });
                    this.props.init();
                  } else if (addRes.code === "1002") {
                    message.warning(addRes.msg);
                  }
                }}
              >
                
              </span>
            </div>
          </div>
        ) : (
          <></>
        )}
        <div
          className="add-new-type"
          onClick={() => {
            this.setState({ addNewTypeStatus: true });
          }}
        >
          <span className="iconfont icon-tianjia"></span>
          <span></span>
        </div>
      </div>
    );
    return (
      <div className="ques-content">
        <div className="title">
          <div className="text-wrap">
            <input
              type="text"
              className="title-text"
              defaultValue={data.question_name}
              onBlur={(e) => {
                setFocusMsg({
                  ...focusMSG,
                  id: focusMSG?.id ? focusMSG.id : -1,
                  option_list: focusMSG?.option_list
                    ? focusMSG.option_list
                    : [],
                  question_name: e.currentTarget.value,
                });
              }}
              onFocus={select}
            />
            <div className="option-tools">
              <Upload
                name="file"
                data={{
                  type: "question",
                  index: -1,
                  id: data.id,
                  imageUid: imageUid,
                  imageType: fileType,
                }}
                maxCount={1}
                accept={acceptFileType}
                action="/api/examEdit/uploadImage"
                headers={{ authorization: "authorization-text" }}
                beforeUpload={imageUploadBefore}
                onChange={imageUploadChange}
              >
                <span className="iconfont icon-tupian" title=""></span>
              </Upload>

              {/* <span className="iconfont icon-jianshao" title=""></span> */}
            </div>
            {data.question_image ? (
              <div>
                <div
                  className="img-container"
                  style={{ maxWidth: 900, display: "inline-block" }}
                >
                  <img
                    style={{ marginLeft: 0, maxWidth: "100%" }}
                    src={`http://192.168.5.104:5008${data.question_image}`}
                    alt=""
                  />
                  <Popconfirm
                    placement="right"
                    title=""
                    onConfirm={async () => {
                      const removeRes = await removeImage({
                        id: data.id ? data.id : -1,
                        index: -1,
                      });
                      // console.log(removeRes);
                      if (removeRes.code === "1001") {
                        init();
                      }
                    }}
                    // onCancel={cancel}
                    okText=""
                    cancelText=""
                  >
                    <span
                      className="iconfont icon-shanchu remove"
                      title=""
                    ></span>
                  </Popconfirm>
                </div>
              </div>
            ) : (
              ""
            )}
            <div className="operation">
              {data.id_in_store && data.id_in_store > 0 ? (
                <span
                  className="iconfont icon-favorite isInStore"
                  title=""
                  onClick={async (e) => {
                    if (data.id_in_store && data.id_in_store > 0) {
                      e.stopPropagation();
                      const cancelRes = await cancelCollect({
                        id: data.id ? data.id : -1,
                        id_in_store: data.id_in_store,
                      });
                      // console.log(cancelRes);
                      if (cancelRes.code === "1001") {
                        message.success("");
                        init();
                      }
                    }
                  }}
                ></span>
              ) : (
                <Popover
                  placement="bottom"
                  content={typeListContent}
                  trigger="click"
                  style={{ height: 500 }}
                  visible={popVisi}
                  onVisibleChange={handleVisibleChange}
                >
                  <span
                    className="iconfont icon-favorite"
                    title=""
                  ></span>
                </Popover>
              )}
              <span
                className="iconfont icon-fuzhi1"
                title=""
                onClick={async () => {
                  const copyRes = await copyFromTemp({
                    exam_id,
                    id: data.id ? data.id : -1,
                  });
                  // console.log(copyRes);
                  if (copyRes.code === "1001") {
                    this.props.init(() => {
                      if (this.props.scrollToBottom) {
                        this.props.scrollToBottom();
                      }
                    });
                  }
                }}
              ></span>
              <span
                className="iconfont icon-shanchu"
                title=""
                onClick={() => {
                  this.setState({ modalState: true });
                }}
              ></span>
            </div>
            <Modal
              title=""
              centered
              visible={modalState}
              onOk={deleteSure}
              onCancel={() => {
                this.setState({ modalState: false });
              }}
              afterClose={() => {
                this.props.setQuesFocus({ index: -1 });
                this.props.init();
              }}
              okText=""
              cancelText=""
            >
              <p></p>
            </Modal>
          </div>
          <span className="must-fill normal">
            <span className="red">*</span>
            <span className="index">{index + 1}</span>
          </span>
        </div>
        {data.option_list
          ? data.option_list
            .filter((item) => item.option_name !== null)
            .map((item, i) => (
              <div className="option-item" key={`${item}_${index}_${i}`}>
                <input
                  type={getQuesType(type)}
                  className="showInp"
                  disabled
                  name={`radio${index}`}
                  id={`radio${index}_${i}`}
                />
                <label htmlFor={`${getQuesType(type)}${index}_${i}`}>
                  <input
                    type="text"
                    // defaultValue={item.option_name}
                    value={item.option_name}
                    className={`inputOption${
                      showSerial ? " showSerial" : ""
                    }`}
                    onFocus={select}
                    onInput={(e) => {
                      // if(e.currentTarget.value==="") return;
                      hiddenHolder[index].option_list[i].option_name =
                          e.currentTarget.value;
                      setHiddenHolder
                        ? setHiddenHolder({ hiddenHolder })
                        : "";
                      if (focusMSG) {
                        focusMSG.option_list[i].option_name =
                            e.currentTarget.value;
                      }
                      setFocusMsg({
                        ...focusMSG,
                        id: focusMSG?.id ? focusMSG.id : -1,
                        option_list: focusMSG?.option_list
                          ? focusMSG.option_list
                          : [],
                      });
                    }}
                  />
                  <div className="hiddenHolder">
                    <span className="serial">
                      {showSerial ? `${indexType[i]}.` : ""}
                    </span>
                    {hiddenHolder[index].option_list[i] ? (
                      <span className="placeHolder">
                        {hiddenHolder[index].option_list[i].option_name}
                      </span>
                    ) : (
                      ""
                    )}
                    {data.answer.indexOf(`${i}`) !== -1 ? (
                      <span
                        style={{
                          color: "#00bf6f",
                          fontSize: 14,
                          float: "right",
                        }}
                      >
                          
                      </span>
                    ) : (
                      ""
                    )}
                  </div>
                  {type !== "3" ? (
                    <div className="option-tools">
                      <Upload
                        name="file"
                        data={{
                          type: "option",
                          index: i,
                          id: data.id,
                          imageUid: imageUid,
                          imageType: fileType,
                        }}
                        maxCount={1}
                        accept={acceptFileType}
                        action="/api/examEdit/uploadImage"
                        headers={{ authorization: "authorization-text" }}
                        beforeUpload={imageUploadBefore}
                        onChange={imageUploadChange}
                      >
                        <span
                          className="iconfont icon-tupian"
                          title=""
                        ></span>
                      </Upload>
                      <span
                        className="iconfont icon-jianshao"
                        title=""
                        onClick={async () => {
                          const { id = -1 } = data;
                          const removeRes = await removeOption({
                            id,
                            index: i,
                          });
                            // console.log(removeRes);
                          if (removeRes.code === "1001") {
                            const { option_list } = data;
                            option_list.splice(i, 1);
                            setFocusMsg({
                              ...focusMSG,
                              id,
                              option_list,
                            });
                            init();
                          }
                        }}
                      ></span>
                    </div>
                  ) : (
                    ""
                  )}
                </label>
                {item.option_image ? (
                  <div
                    className="img-container"
                    style={{
                      maxWidth: 900,
                      display: "inline-block",
                      marginLeft: 51,
                    }}
                  >
                    <img
                      style={{ marginLeft: 0, maxWidth: "100%" }}
                      src={`http://192.168.5.104:5008${item.option_image}`}
                      alt=""
                    />
                    <Popconfirm
                      placement="right"
                      title=""
                      onConfirm={async () => {
                        const removeRes = await removeImage({
                          id: data.id ? data.id : -1,
                          index: i,
                        });
                        if (removeRes.code === "1001") {
                          init();
                        }
                      }}
                      okText=""
                      cancelText=""
                    >
                      <span
                        className="iconfont icon-shanchu remove"
                        title=""
                      ></span>
                    </Popconfirm>
                  </div>
                ) : (
                  ""
                )}
              </div>
            ))
          : ""}
        {data.question_type !== "3" ? (
          <div className="addOption">
            <Button
              style={{ margin: "5px 0 0 50px" }}
              type="primary"
              ghost
              onClick={async () => {
                const { id = -1 } = data;
                if (data.option_list.length >= 10) {
                  message.warning("10");
                } else {
                  const addRes = await addOption({ id });
                  if (addRes.code === "1001") {
                    // console.log(addRes.optionListRes);
                    const optionList = addRes.optionListRes
                      .filter(
                        (item, index) => item[`option${index + 1}`] !== null
                      )
                      .map((item, index) => ({
                        [`option_name`]: item[`option${index + 1}`],
                        [`option_image`]: item[`option${index + 1}_image`],
                      }));
                    // console.log(optionList);
                    setFocusMsg({
                      ...focusMSG,
                      id,
                      option_list: optionList,
                    });
                    init();
                  }
                }
              }}
            >
              
            </Button>
          </div>
        ) : (
          ""
        )}
      </div>
    );
  }
}
interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(Choice);
