import React, { Component } from "react";
import { Button, message, TimePicker } from "antd";
import { connect } from "react-redux";
import copy from "copy-to-clipboard";
import QRcode from "qrcode.react";
import { getIssueStatus, issueExam } from "../../apis/examEdit";
import action from "../../store/actions";
import moment from "moment";

interface IProps {
  exam_id?: number;
  examHostLink?: string;
  setExamStatus?: (payload: { examStatus: boolean }) => void;
}
type PickerValue = moment.Moment | null | undefined;
interface IState {
  issueState: boolean;
  examLinkRandom: string;
  // duration: string;
  duration: PickerValue;
}
class IssueProject extends Component<IProps, IState> {
  state = {
    issueState: false,
    examLinkRandom: "",
    // duration: "",
    duration: null,
  };
  async componentDidMount() {
    const {
      exam_id,
      setExamStatus = () => {
        ("");
      },
    } = this.props;
    const statusRes = await getIssueStatus({ exam_id: exam_id ? exam_id : -1 });
    // console.log(statusRes);
    if (statusRes.code === "1001" && statusRes.isStart === 1) {
      setExamStatus({ examStatus: true });
      this.setState({ issueState: true, examLinkRandom: statusRes.examLink });
    } else {
      setExamStatus({ examStatus: false });
    }
  }
  render() {
    const { issueState, examLinkRandom } = this.state;
    const duration:PickerValue =this.state.duration;
    const {
      exam_id,
      examHostLink,
      setExamStatus = () => {
        ("");
      },
    } = this.props;
    return (
      <div
        className="exam-content"
        onClick={(e) => {
          e.stopPropagation();
        }}
      >
        <div className="issue-box">
          {!issueState ? (
            <div
              className="not-issue"
              style={{ margin: "0 auto", paddingTop: 240, textAlign: "center" }}
            >
              <div className="title"></div>
              <div>
                <div style={{display:"inline-block"}}>
                  <span></span>
                  <TimePicker
                    placeholder=""
                    value={duration}
                    minuteStep={15}
                    secondStep={10}
                    // renderExtraFooter={() => <></>}
                    onChange={(e) => {
                      this.setState({
                        duration: moment(
                          `${e?.hours()}:${e?.minutes()}:${e?.seconds()}`,
                          "HH:mm:ss"
                        ),
                      });
                    }}
                  />
                </div>
              </div>
              <Button
                type="primary"
                size="large"
                style={{ width: 160, height: 40, marginTop: 20 }}
                onClick={async () => {
                  // console.log(duration);
                  if (!duration) {
                    message.warning({
                      content: "",
                      duration: 0.7,
                    });
                    return;
                  }
                  const issueRes = await issueExam({
                    exam_id: exam_id ? exam_id : -1,duration
                  });
                  if (issueRes.code === "1001") {
                    setExamStatus({ examStatus: true });
                    this.setState({
                      issueState: true,
                      examLinkRandom: `${issueRes.examLink}`,
                    });
                  } else {
                    message.error("");
                  }
                }}
              >
                
              </Button>
            </div>
          ) : (
            <div
              className="issued"
              style={{ margin: "0 auto", paddingTop: 100 }}
            >
              <div className="container">
                <div className="title">
                  <span className="title-text"></span>
                  <span className="describe">
                    
                  </span>
                </div>
                <div className="issue-share">
                  <div className="link">
                    <h3></h3>
                    <div className="link-text">
                      <input
                        type="text"
                        readOnly
                        disabled
                        value={`${examHostLink}/${examLinkRandom}`}
                      />
                      <Button
                        type="primary"
                        style={{
                          width: 80,
                          height: 40,
                          borderBottomColor: "#2672ff",
                          borderRadius: "0 3px 3px 0",
                          fontSize: 16,
                        }}
                        onClick={() => {
                          message.success({
                            content: "",
                            duration: 0.8,
                            style: {
                              position: "relative",
                              top: 50,
                            },
                          });
                          copy(`${examHostLink}/${examLinkRandom}`);
                        }}
                      >
                        
                      </Button>
                      <Button
                        style={{
                          width: 80,
                          height: 40,
                          borderRadius: "3px",
                          marginLeft: 10,
                          borderColor: "#2672ff",
                          color: "#2672ff",
                          fontSize: 16,
                        }}
                        onClick={() => {
                          window.open(`${examHostLink}/${examLinkRandom}`);
                        }}
                      >
                        
                      </Button>
                    </div>
                  </div>
                  <div
                    className="code"
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <h3 style={{ marginBottom: 10 }}></h3>
                    <QRcode value={`${examHostLink}/${examLinkRandom}`} />
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}
interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(IssueProject);
