import React, { Component } from "react";
import { connect } from "react-redux";
import { Button, Popover } from "antd";
import action from "../../../store/actions";
import { insertQuestion, getExam } from "../../../apis/examEdit";
interface IProps {
  exam_id?: number;
  // init:()=>void
  setOriginList: (payload: { questionList: Array<never> }) => void;
  login_id?: number;
  scrollToBottom?: () => void;
}
interface IState {
  typeList: ITypeItem[];
  msgList: ITypeItem[];
}
interface ITypeItem {
  name: string;
  icon: string;
}
class QuestionTypeList extends Component<IProps, IState> {
  state = {
    typeList: [
      { name: "", icon: "icon-danxuan", question_type: "1" },
      { name: "", icon: "icon-duoxuankuang", question_type: "2" },
      { name: "", icon: "icon-panduan2", question_type: "3" },
      { name: "", icon: "icon-tiankong", question_type: "4" },
      { name: "", icon: "icon-jianda2", question_type: "5" },
    ],
    msgList: [
      { name: "", icon: "icon-xingmingzhengjian" },
      { name: "", icon: "icon-dianhua" },
      { name: "", icon: "icon-banjiguanli" },
    ],
  };
  addQuestion = async (type: string, exam_id: number | undefined) => {
    const { scrollToBottom } = this.props;
    const insertRes = await insertQuestion({
      question_type: type,
      exam_id,
      creator_id: this.props.login_id,
    });
    // console.log(insertRes);
    if (insertRes.code === "1001") {
      const examRes = await getExam({ exam_id });
      this.props.setOriginList({
        questionList: examRes.question_list,
      });
      if (scrollToBottom) scrollToBottom();
    }
  };
  render() {
    const { typeList } = this.state;
    const { addQuestion } = this;
    const { exam_id } = this.props;
    return (
      <div className="type-list">
        <div className="list-title"></div>
        <div className="list-content">
          {typeList.map((item) => (
            <div key={item.icon} style={{ position: "relative" }}>
              <Button
                size="small"
                style={{
                  marginLeft: 20,
                  width: 160,
                  height: 40,
                  fontSize: "16px",
                  textAlign: "left",
                }}
                onClick={(e) => {
                  e.stopPropagation();
                  addQuestion(item.question_type, exam_id);
                }}
              >
                <i
                  style={{ fontStyle: "normal", fontSize: 18 }}
                  className={`iconfont ${item.icon}`}
                ></i>
                <span className="itemName">{item.name}</span>
              </Button>
              <Popover
                placement="right"
                trigger="hover"
                overlayClassName="store-question-preview"
                content={
                  <div className="preview-question">
                    <div className="preview-title">
                      {item.question_type === "1" ? "" : ""}
                      {item.question_type === "2"
                        ? " ()"
                        : ""}
                      {item.question_type === "3"
                        ? ""
                        : ""}
                      {item.question_type === "4" ? "" : ""}
                      {item.question_type === "5" ? "" : ""}
                    </div>
                    {item.question_type === "5" ? (
                      <textarea cols={21} rows={2}></textarea>
                    ) : (
                      ""
                    )}
                    {item.question_type === "1" ||
                    item.question_type === "2" ? (
                        <ul className="preview-option">
                          {[1, 2, 3, 4].map((option) => (
                            <li
                              key={option}
                              style={{
                                display: "flex",
                                alignItems: "center",
                                padding: "3px 0",
                              }}
                            >
                              <input
                                style={{ marginRight: 5 }}
                                readOnly
                                disabled
                                type={
                                  item.question_type === "2"
                                    ? "checkbox"
                                    : "radio"
                                }
                              />
                              <span>{`${option}`}</span>
                            </li>
                          ))}
                        </ul>
                      ) : (
                        ""
                      )}
                    {item.question_type === "3" ? (
                      <ul className="preview-option">
                        {["", ""].map((option) => (
                          <li
                            key={option}
                            style={{
                              display: "flex",
                              alignItems: "center",
                              padding: "3px 0",
                            }}
                          >
                            <input
                              style={{ marginRight: 5 }}
                              readOnly
                              disabled
                              type={"radio"}
                            />
                            <span>{`${option}`}</span>
                          </li>
                        ))}
                      </ul>
                    ) : (
                      ""
                    )}
                    {item.question_type === "4" ? (
                      <ul className="preview-option">
                        {[1, 2].map((option) => (
                          <li
                            key={option}
                            style={{
                              display: "block",
                              alignItems: "center",
                              padding: "3px 0",
                            }}
                          >
                            <span>{`${option}`}</span>
                            <input
                              type="text"
                              readOnly
                              disabled
                              style={{
                                border: "1px solid #ccc",
                                display: "block",
                              }}
                            />
                          </li>
                        ))}
                      </ul>
                    ) : (
                      ""
                    )}
                  </div>
                }
              >
                <span
                  className="iconfont icon-chakan"
                  style={{
                    position: "absolute",
                    width: 30,
                    textAlign: "center",
                    fontSize: 20,
                    top: "50%",
                    transform: "translateY(-50%)",
                    right: -60,
                    cursor: "pointer",
                  }}
                ></span>
              </Popover>
            </div>
          ))}
        </div>
        {/* <div className="list-title"></div>
        <div className="list-content">
          {msgList.map((item) => (
            <Button
              key={item.icon}
              size="small"
              style={{ width: "110px", height: "30px", fontSize: "14px" }}
            >
              <i
                style={{ fontStyle: "normal" }}
                className={`iconfont ${item.icon}`}
              ></i>
              <span className="itemName">{item.name}</span>
            </Button>
          ))}
        </div> */}
      </div>
    );
  }
}
interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(QuestionTypeList);
