const canvasSupported = isCanvasSupported();

function dataURItoBlob(dataURI) {
  // convert base64 to raw binary data held in a string
  let byteString, mimestring;

  if (dataURI.split(",")[0].indexOf("base64") !== -1) {
    byteString = atob(dataURI.split(",")[1]);
  } else {
    byteString = decodeURI(dataURI.split(",")[1]);
  }

  mimestring = dataURI.split(",")[0].split(":")[1].split(";")[0];

  const content = [];
  for (let i = 0; i < byteString.length; i++) {
    content[i] = byteString.charCodeAt(i);
  }

  return new Blob([new Uint8Array(content)], { type: mimestring });
}
function imgScale(src, scale, cbk) {
  if (!src) return cbk(false);
  const _canvas = document.createElement("canvas");
  const tImg = new Image();
  tImg.onload = function () {
    const _context = _canvas.getContext("2d");
    _canvas.width = tImg.width;
    _canvas.height = tImg.height;
    _context.drawImage(tImg, 0, 0);
    const type = "image/jpeg";
    src = _canvas.toDataURL(type, scale);
    const blob = dataURItoBlob(src);
    cbk(blob);
  };
  tImg.src = src;
}
function isCanvasSupported() {
  const elem = document.createElement("canvas");
  return !!(elem.getContext && elem.getContext("2d"));
}
export const support = canvasSupported;
export function zip (files, opt, cbk) {
  opt = opt || {};
  const scale = opt.scale;
  if (!canvasSupported) return cbk(files);
  if (!scale || 1 == scale) return cbk(files);
  let files_count = files.length,
    ret = [];
  for (let i = 0, j = files.length; i < j; i++) {
    const fReader = new FileReader();
    fReader.onload = function (e) {
      const result = e.target.result;
      imgScale(result, scale, function (file) {
        file && ret.push(file);
        files_count--;
        if (files_count <= 0) cbk && cbk(ret);
      });
    };
    fReader.readAsDataURL(files[i]);
  }
}
