import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import { Popover, Steps, Button, message, Tooltip } from "antd";
import { RouteComponentProps } from "react-router-dom";
import ExamLeftNav from "../../components/exam-left-nav";
import ExamRightNav from "../../components/exam-right-nav";
import Choices from "../../components/question-store/choices";
import GapFilling from "../../components/question-store/gap-filling";
import ShortAnswer from "../../components/question-store/short-answer";
import IssueProject from "./issue-project";
import DataReport from "./data-report";
import {
  getExam,
  quesEditItem,
  getQuesStore,
  editExamMSG,
} from "../../apis/examEdit";
import { IStep } from "../../store/reducers/examEdit";
import action from "../../store/actions";
import "./index.less";
interface IProps extends RouteComponentProps {
  exam_id: number;
  login_id: number;
  setExamId: (payload: { exam_id: number }) => void;
  setOriginList: (payload: { questionList: Array<IQuesData> }) => void;
  questionListOrigin: IQuesData[];
  questionList: IQuesData[];
  steps: IStep[];
  current: number;
  setStepStatus: (payload: { index: number }) => void;
  question_focus: number;
  setQuesFocus: (payload: { index: number }) => void;
  setQuesType: (payload: { ques_type: string }) => void;
  focusMSG: IFocusQuestion;
  setFocusMsg: (payload: IFocusQuestion) => void;
  examHostLink?: string;
  setTypeList: (payload: { typeList: ITypeList }) => void;
  setQuestionStore: (payload: {
    question_store: IQuestionOriginItem[];
  }) => void;
  examStatus: boolean;
}
interface IState {
  examTitle?: string;
  welcomeHolder?: string;
  focusTemp: number;
}
export interface IQuestionItem {
  isMessage?: boolean;
  questionType?: string;
  questionName?: string;
}
export const getTypeName = (type: string) => {
  switch (type) {
  case "1":
    return "";
  case "2":
    return "";
  case "3":
    return "";
  case "4":
    return "";
  case "5":
    return "";
  default:
    return "";
  }
};
class Examination extends Component<IProps, IState> {
  state = {
    examTitle: "",
    welcomeHolder: "",
    focusTemp: -1,
  };
  init = async (backToList?: () => void) => {
    const {
      current,
      setOriginList,
      setStepStatus,
      exam_id,
      login_id = -1,
      setTypeList,
      setQuestionStore,
    } = this.props;
    const res: IExamListResponse = await getExam({ exam_id });
    if (res && res.examItem && res.examItem.id) {
      this.setState({
        examTitle: res.examItem.exam_name || "",
        welcomeHolder: res.examItem.welcome || "",
      });
    }
    const typeListRes = await getQuesStore({ creator_id: login_id });
    // console.log(typeListRes);
    if (typeListRes.code === "1001") {
      // console.log(typeListRes);
      setQuestionStore({ question_store: typeListRes.question_store });
      setTypeList({ typeList: typeListRes.store_type });
    }
    setOriginList({
      questionList: res.question_list,
    });
    setStepStatus({ index: current });
    if (backToList) {
      backToList();
    }
  };
  get totalScore() {
    const { questionList } = this.props;
    return questionList.reduce(
      (prev, next) => (prev += next.question_score),
      0
    );
  }
  getQuesType = (index: number, type: string, IQuesData: IQuesData) => {
    switch (type) {
    case "1":
      return (
        <Choices
          index={index}
          type={type}
          question_data={IQuesData}
          init={this.init}
        />
      );
    case "2":
      return (
        <Choices
          index={index}
          type={type}
          question_data={IQuesData}
          init={this.init}
        />
      );
    case "3":
      return (
        <Choices
          index={index}
          type={type}
          question_data={IQuesData}
          init={this.init}
        />
      );
    case "4":
      return (
        <GapFilling
          index={index}
          type={type}
          question_data={IQuesData}
          init={this.init}
        />
      );
    case "5":
      return (
        <ShortAnswer
          index={index}
          type={type}
          question_data={IQuesData}
          init={this.init}
        />
      );
    default:
      return (
        <Choices
          index={index}
          type={type}
          question_data={IQuesData}
          init={this.init}
        />
      );
    }
  };
  getTypeName = getTypeName;
  componentDidMount() {
    this.init();
  }
  render() {
    const { Step } = Steps;
    const {
      exam_id,
      steps,
      setStepStatus,
      current, //  0 1 2
      setQuesFocus,
      question_focus,
      setQuesType,
      focusMSG,
      setFocusMsg,
      questionList,
      examStatus,
    } = this.props;
    const { examTitle, welcomeHolder, focusTemp } = this.state;
    const { getQuesType, getTypeName, totalScore } = this;
    return (
      <div
        className="exam-page"
        onClick={async () => {
          const editRes = await quesEditItem({ ...focusMSG, exam_id });
          if (editRes.code === "1001") {
            this.init();
          }
          setQuesFocus({ index: -1 });
        }}
      >
        <div
          className="exam-header"
          onClick={(e) => {
            e.stopPropagation();
          }}
        >
          <Tooltip
            placement="bottomLeft"
            title=""
            arrowPointAtCenter
          >
            <span
              className="backToList iconfont icon-houtui1"
              onClick={async (e) => {
                e.stopPropagation();
                if (current !== 0 || !focusMSG || focusMSG.id < 0) {
                  this.props.history.push("/layout/home/list");
                  return;
                }
                const editRes = await quesEditItem({ ...focusMSG, exam_id });
                setQuesFocus({ index: -1 });
                if (editRes.code === "1001") {
                  this.init(() => {
                    this.props.history.push("/layout/home/list");
                  });
                }
              }}
            ></span>
          </Tooltip>
          <div className="exam-title-msg">{examTitle}</div>
          <div className="exam-step" style={{ marginLeft: 130 }}>
            <Steps
              type="navigation"
              current={current}
              className="site-navigation-steps"
            >
              {steps.map((item: IStep, index: number) => (
                <Step
                  status={item.status}
                  title={item.title}
                  key={item.current}
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    if (this.props.examHostLink && index === 0 && examStatus) {
                      message.warning({
                        content: ",",
                        duration: 0.7,
                        style: {
                          position: "relative",
                          top: 50,
                        },
                      });
                    } else {
                      setStepStatus({ index });
                    }
                  }}
                />
              ))}
            </Steps>
          </div>
          <div
            className="save-issue"
            style={{
              paddingLeft: `${current !== 0 ? "184.38px" : "0"}`,
            }}
          >
            {current === 0 ? (
              <>
                <Button
                  style={{
                    borderColor: "#2672ff",
                    color: "#2672ff",
                    marginRight: 10,
                  }}
                >
                  
                </Button>
                <Button
                  type="primary"
                  style={{
                    marginRight: 10,
                  }}
                  onClick={(e) => {
                    e.stopPropagation();
                    setStepStatus({ index: 1 });
                  }}
                >
                  
                </Button>
              </>
            ) : (
              ""
            )}
            <Popover
              placement="bottomRight"
              content={
                <span
                  className="home-logout"
                  onClick={() => {
                    this.props.history.push("/login");
                  }}
                >
                  
                </span>
              }
              trigger="hover"
            >
              <span className="iconfont icon-wode2"></span>
            </Popover>
          </div>
        </div>
        {current === 0 ? (
          <div className="exam-content">
            <div className="left-nav">
              <ExamLeftNav init={this.init} />
            </div>
            <div className="exam-body">
              <div className="inner">
                <div className="exam-titleAndWelcome">
                  <div className="title-wrap">
                    <div
                      className="title-text"
                      contentEditable="true"
                      dangerouslySetInnerHTML={{ __html: examTitle }}
                      onBlur={async (e) => {
                        const editRes = await editExamMSG({
                          exam_id,
                          examTitle: e.currentTarget.innerText,
                          welcomeHolder,
                        });
                        // console.log(editRes);
                        if (editRes.code === "1001") {
                          this.init();
                        }
                      }}
                    ></div>
                  </div>
                  <div className="welcome-wrap">
                    <div
                      className="title-text"
                      contentEditable="true"
                      dangerouslySetInnerHTML={{ __html: welcomeHolder }}
                      onBlur={async (e) => {
                        // const editRes =
                        await editExamMSG({
                          exam_id,
                          examTitle,
                          welcomeHolder: e.currentTarget.innerText,
                        });
                        // console.log(editRes);
                        // console.log(exam_id, e.currentTarget.innerText);
                      }}
                    ></div>
                  </div>
                  <div className="total-score">
                    <h3></h3>
                    <p>{totalScore}</p>
                  </div>
                </div>
                <div className="msg-question-list">
                  <div className="question-item">
                    <div className="ques-content">
                      <div className="title">
                        
                        <span className="must-fill">
                          <span className="red">*</span>
                        </span>
                      </div>
                      <input type="text" className="showInp" disabled />
                    </div>
                  </div>
                  <div className="question-item">
                    <div className="ques-content">
                      <div className="title">
                        
                        <span className="must-fill">
                          <span className="red">*</span>
                        </span>
                      </div>
                      <input type="text" className="showInp" disabled />
                    </div>
                  </div>
                  <div className="question-item">
                    <div className="ques-content">
                      <div className="title">
                        
                        <span className="must-fill">
                          <span className="red">*</span>
                        </span>
                      </div>
                      <input type="text" className="showInp" disabled />
                    </div>
                  </div>
                </div>
                <div className="normal-question-list">
                  {questionList.map((item: IQuesData, index: number) => (
                    <div
                      className={`question-item${
                        question_focus === item.id ? " question_focus" : ""
                      }`}
                      key={item.id}
                      onClick={async (e) => {
                        e.stopPropagation();
                        if (focusTemp !== item.id && focusTemp !== -1) {
                          const editQues = await quesEditItem({
                            ...focusMSG,
                            exam_id,
                          });
                          if (editQues.code === "1001") {
                            this.init();
                          }
                          setFocusMsg({
                            ...item,
                            id: item.id ? item.id : -1,
                          });
                        } else if (focusTemp === -1) {
                          setFocusMsg({
                            ...item,
                            id: item.id ? item.id : -1,
                          });
                        }
                        setQuesFocus({ index: item.id ? item.id : -1 });
                        setQuesType({
                          ques_type: getTypeName(item.question_type),
                        });
                        this.setState({ focusTemp: item.id ? item.id : -1 });
                      }}
                    >
                      {getQuesType(index, item.question_type, item)}
                    </div>
                  ))}
                </div>
              </div>
            </div>
            <div
              className="right-nav"
              onClick={(e) => {
                e.stopPropagation();
              }}
            >
              <ExamRightNav init={this.init} />
            </div>
          </div>
        ) : current === 1 ? (
          <IssueProject />
        ) : current === 2 ? (
          <DataReport />
        ) : (
          <></>
        )}
      </div>
    );
  }
}
interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(Examination);
